import React from 'react';
import SettingsItem from '../../04_ui_kit/SettingsItem/SettingsItem';

export default function SettingsList({ settings, func, updateSiteConfig, settingsModals }) {

  return (
    <>
      <div className="settings-list">
        {settings.length
          ? settings.map((el) => {
              return (
                <SettingsItem
                  key={el.title}
                  title={el.title}
                  descr={el.descr}
                  func={func}
                  updateSiteConfig ={updateSiteConfig }
                  settingsModals={settingsModals}
                />
              );
            })
          : null}
      </div>
    </>
  );
}
