import React from 'react';

export default function Modal({ data, settingsModals }) {
  let Setting;
  settingsModals.forEach((el) => {
    if (el.name === data.name) {
      Setting = el.setting;
    }
  });
  return (
    <div className="modal">
      <div className="modal-title">{data.name}</div>
      <Setting data={data} />
    </div>
  );
}
