import React from 'react';
import { Link } from 'react-router-dom';
import { useEffect } from 'react';

let flipers;

function takeFlipers() {
  flipers = Array.prototype.slice.call(document.querySelectorAll('.card'));
}
function backFlip() {
  flipers.forEach((el) => {
    let fliperFace = el.querySelector('.card-face');
    let fliperBack = el.querySelector('.card-back');
    if (el.classList.contains('is-flipped')) {
      fliperBack.classList.add('disappear');
      setTimeout(() => {
        fliperBack.style.display = 'none';
        el.classList.toggle('is-flipped');
        fliperFace.classList.add('appear');
        setTimeout(() => {
          fliperFace.style.display = 'flex';
          fliperBack.classList.remove('disappear');
          setTimeout(() => {
            fliperFace.classList.remove('appear');
          }, 1000);
        }, 500);
      }, 850);
    }
  });
}
function flip(e) {
  backFlip();

  let card = e.target.closest('.card');
  let face = card.querySelector('.card-face');
  let back = card.querySelector('.card-back');

  face.classList.add('disappear');
  setTimeout(() => {
    face.style.display = 'none';
    card.classList.toggle('is-flipped');
    back.classList.add('appear');
    setTimeout(() => {
      back.style.display = 'block';
      face.classList.remove('disappear');
      setTimeout(() => {
        back.classList.remove('appear');
      }, 1000);
    }, 500);
  }, 850);
}

export default function Card({ pic, title, descr, imgClass }) {
  useEffect(() => {takeFlipers()},[])
  return (
    <div className="card">
      <div className="card-face">
        <div className="card-title">{title}</div>
        <div className={imgClass}>{pic}</div>
        <div className="card-more button" onClick={(e) => flip(e)}>
          {' '}
          Подробнее{' '}
        </div>
      </div>
      <div className="card-back">
        <div className="card-title">{title}</div>
        <div className="card-descr">{descr}</div>
        <div className="back-buttons flex-row-space-around">
          <div className="button" onClick={() => backFlip()}>
            Назад{' '}
          </div>
          <Link className="button" to="/services">
            Подробнее о наших возможностях...
          </Link>
        </div>
      </div>
    </div>
  );
}
