import React from 'react';
import { useNavigate } from 'react-router-dom';
import line from  './line.png'
import logo from './logo.png';


let Scroll = require('react-scroll');
let scroll = Scroll.animateScroll;
 let homePath = "/"

 export default function Header() {
  let navHome = useNavigate()

  return (
    <header className="header">
      <img src={line} className="line" alt="line" />
       <img
        className="logo"
        alt="logo"
        src={logo}
        id="Logo"
        onClick={() =>window.location.pathname === homePath ? scroll.scrollToTop() : navHome('/')}
      /> 
      <div className='number'>Телефон для связи: <a href="tel:2831544">283-15-44</a></div>
      <a className='telSmall' href="tel:2831544">283-15-44</a>
      
    </header>
  );
}
