import React from 'react';
import 'react-tooltip/dist/react-tooltip.css';
import { Tooltip } from 'react-tooltip';
import Modal from '../Modal/Modal';
import { useState } from 'react';

export default function SettingsItem({ title, descr, func,updateSiteConfig, settingsModals  }) {
  const [modal, setModal] = useState(false);

  function change(e) {
    let funcName = e.target.previousElementSibling.innerText;
    func.forEach(el => {
      if (el.name === funcName) {
        return func = el.func
      }
    });

    setModal({
      name: funcName,
      update: function update() {
        setModal(false);
      },
      func: func,
      updateSiteConfig : updateSiteConfig,
    });
  }

  return (
    <>
      <div className="settings-item">
        <h3 className="setting-title">{title}</h3>

        {descr.length
          ? descr.map((el) => {
              return (
                <div className="setting-descr" key={el.name}>
                  <div className="setting-name">{el.name}</div>
                  <div id={el.id} className="setting-change button" onClick={change}>
                    Изменить
                  </div>
                  <Tooltip anchorId={el.id} place="left" content={el.tooltip} />
                </div>
              );
            })
          : null}
        {modal && <Modal data={modal} settingsModals={settingsModals}/>}
      </div>
    </>
  );
}
