import React from 'react';
import ContentLayout from '../../02_modules/ContentLayout/ContentLayout'
import SettingsList from '../../03_components/SettingsList/SettingsList';
import { settingsConst, settingsFunc } from './SettingsConst';
import { settingsModals } from './settingsModals';




export default function SettingsPage({updateSiteConfig}) {
  const settings = () => ( <SettingsList settings={settingsConst} func={settingsFunc} updateSiteConfig={updateSiteConfig} settingsModals={settingsModals}/>)


  return (
    <>
    <ContentLayout title={'Настройки'} titletype={'h1'} className={'settings'} Component={settings}/>
    </>
  );
}
