import React from 'react';
import Card from '../../04_ui_kit/Card/Card';

export default function CardList({ array }) {
  return (
    <>
      <div className="cards-list">
        {array.length
          ? array.map((el) => {
              return (
                <Card
                  imgClass={el.imgClass}
                  pic={el.pic}
                  title={el.title}
                  descr={el.descr}
                  key={el.id}
                />
              );
            })
          : null}
      </div>
    </>
  );
}
