import React from 'react';
import { Link } from 'react-router-dom';

export default function ReadList({ array }) {

  return (
    <>
      {array.length
        ? array.map((el) => {
            return (
              <Link className='button' key={el.to} to={el.to}>
                {el.title}
              </Link>
            );
          })
        : null}
    </>
  );
}
