import React from 'react'
import AccordionList from '../../03_components/AccordionList/AccordionList'
import EndOfRead from '../../03_components/ArticleComponents/EndOfRead/EndOfRead'
import BackLinks from '../../04_ui_kit/BackLinksArticles/BackLinks'

export default function ArticleLayout({title, inner, data, serviceName, Do, path}) {
  return (
    <>
    <article className="article-inner">
        <h1>{title}</h1>
        {serviceName ? <AccordionList data={data} /> : null}
        {inner}
        {serviceName ? <EndOfRead
          serviceName={serviceName}
          Do={Do}
          path={path}
        /> : <BackLinks path={path} />}
      </article>
    </>
  )
}
